<template>
  <div id="app">
    <header>
        <h1>My Music</h1>
    </header>
    <main>
        <section class="player">
          <div class="player__image">
          <img v-bind:src="current.image">
          </div>
          <div class="player__title">
          <h2 >{{ current.title }}</h2>
          <h3> {{ current.artist }}</h3>
          </div>
        </section>
        <div class="player__control">
          <button class="prev" @click="prev">Prev</button>
          <button class="play" v-if="!isPlaying" @click="play">Play</button>
          <button class="pause" v-else @click="pause">Pause</button>
          <button class="next" @click="next">Next</button>
        </div>
        <section class="player__playlist">
          <h3>The Playlist</h3>
          <div class="playlist__container">
          <button v-for="song in songs" :key="song.src" @click="play(song)" :class="(song.src == current.src) ? 'song playing' : 'song'">{{ song.title }} - {{ song.artist }}</button>
          </div>
        </section>
    </main>
  </div>
</template>

<script>


export default {
  name: 'app',
  data () {
    return {
      current: {},
      index: 0,
      isPlaying: false,
      noImage: true,
      songs: [
        {
          title: 'Attack',
          artist: 'Nifra',
          src: require('./assets/Nifra - Attack.mp3'),
          image: require('./assets/images/nifra.jpg'),
        },
        {
          title: 'Up On My Shoulders',
          artist: 'Nifra',
          src: require('./assets/Nifra - Upon my Shoulders.mp3'),
          image: require('./assets/images/nifra.jpg'),

        },
        {
          title: 'Puzzle Piece',
          artist: 'Space RockerZ & Tania Zygar',
          src: require('./assets/Puzzle Piece.mp3'),
          image: require('./assets/images/puzzle piece.jpg')
        },
        {
          title: 'White Sand',
          artist: 'Sunlounger',
          src: require('./assets/Sunlounger - White Sand.mp3'),
          image: require('./assets/images/sunlounger.jpeg')
        }
      ],
      player: new Audio()
    }
  },
  methods: {
    play (song) {
      if (typeof song.src != "undefined") {
        this.current = song;
        this.player.src = this.current.src;
      }

      this.player.play();
      this.player.addEventListener('ended', function (){
        this.index++;
        if (this.index > this.songs.length -1) {
        this.index = 0;
      }

      this.current = this.songs[this.index];
      this.play(this.current);
      }.bind(this));
      this.isPlaying = true;
    },
    pause () {
      this.player.pause();
      this.isPlaying = false;
    },
    next () {
      this.index++;
      if (this.index > this.songs.length -1) {
        this.index = 0;
      }

      this.current = this.songs[this.index];
      this.play(this.current);
    },
    prev () {
      this.index--;
      if (this.index < 0) {
        this.index = this.songs.length - 1;
      }
      
      this.current = this.songs[this.index];
      this.play(this.current);
      }
  },
  created () {
    this.current = this.songs[this.index];
    this.player.src = this.currenct.src;
  }
}
</script>

